.weather{
    place-self: center;
    padding: 40px;
    border-radius: 5px;
    background-image: linear-gradient(45deg,#2f4680,#500ae4);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search-bar{
    display: flex;
    align-items: center;
    gap: 10px;
}
.search-bar input{
    height: 45px;
    border: none;
    outline: none;
    border-radius: 20px;
    color: #626262;
    background-color: #ebfffc;
    padding-left: 15px;
    font-size: 16px;
    font-weight: 500;
}
.search-bar img{
    width: 45px;
    cursor: pointer;
    background-color: #ebfffc;
    padding: 12px;
    border-radius: 50%;
}
.weather-icon{
    width: 120px;
    margin: 20px 0;
}
.temp{
    font-size: 50px;
    color: white;
    line-height: 1;
}
.location{
    font-size: 30px;
    color: white;
}
.weather-data{
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  color: white;
  margin-top: 40px;
}
.col{
    display: flex;
    align-items: flex-start;
    font-size: 18px;
    gap: 10px;
}
.col span{
    display: block;
    font-size: 16px;
}
.col img{
    width: 26px;
    margin-top: 10px;
}